.books {
  grid-column: content-start/content-end;
  display: flex;
  padding: 1rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 4rem;
}
.filters {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
  margin-left: 20rem;
  width: 80%;
}
.filter {
  margin: 1rem 4rem;
}
.corner-image {
  position: absolute;
  top: 2rem;
  left: 2rem;
  height: 20rem;
  width: 20rem;
}
.filter-title {
  font-size: 2rem;
  margin-bottom: 0.5rem;
}
.filter-input {
  margin-bottom: 2rem;
  width: 25rem;
  height: 5rem;
  padding: 1rem;
  font-size: 2rem;
  border-radius: 0.5rem;
  outline: none;
  border: solid 1px;
  background-color: #fff;
}
.filter-input-small {
  margin-bottom: 2rem;
  width: 10rem;
  height: 5rem;
  padding: 1rem;
  font-size: 2rem;
  border-radius: 0.5rem;
  outline: none;
  border: solid 1px;
}
.price-filter {
  width: 25rem;

  display: flex;
  justify-content: space-between;
}
.price-range-text {
  line-height: 5rem;
  height: 100%;
  font-size: 2rem;
}

.posts-container {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.paginate {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 5rem;
}
.pagi-button {
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.25),
    0 0.2rem 0.2rem rgba(0, 0, 0, 0.22);
  background-color: rgb(255, 255, 255);
  height: 3rem;
  width: 3rem;
  margin: 0.7rem;
  margin-bottom: 5rem;
  color: rgb(129, 86, 214);
  font-size: 2.5rem;
  border-radius: 0.5rem;
  border: none;
  outline: none;
  cursor: pointer;
  &:hover {
    background-color: rgb(210, 192, 247);
  }
  &:active {
    transform: translateY(-0.2rem);
    box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
      0 0.2rem 0.2rem rgba(0, 0, 0, 0.22);
  }
}
.pagi-button-selected {
  box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
    0 0.5rem 0.5rem rgba(0, 0, 0, 0.22);
  background-color: rgb(129, 86, 214);
  height: 3rem;
  width: 3rem;
  margin: 0.7rem;

  color: white;
  font-size: 2.5rem;
  border-radius: 0.5rem;
  border: none;
  outline: none;
  cursor: pointer;
  &:hover {
    background-color: rgb(89, 47, 173);
  }
  &:active {
    transform: translateY(-0.2rem);
    box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
      0 0.2rem 0.2rem rgba(0, 0, 0, 0.22);
  }
}
.no-match {
  text-align: center;
  font-size: 5rem;
}
@media (max-width: 1024px) {
  .books {
    grid-column: page-start/page-end;
  }
  .filter {
    margin: 1rem 1rem;
  }
}
@media (max-width: 600px) {
  .books {
    grid-column: page-start/page-end;
  }
  .corner-image {
    height: 12rem;
    width: 12rem;
  }
  .filters {
    margin: 0;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }
}
// animations

.books-animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.fade-in {
  -webkit-animation-name: fade-in;
  animation-name: fade-in;
}
