.navigation {
  position: fixed;
  top: 0;
  left: 88%;
  width: 12%;
  height: 100%;
  z-index: 10;
}
.logo {
  position: relative;
  top: 3%;
  right: 3%;
}
.logo-img {
  height: 100%;
  width: 100%;
}
.nav-menu {
  position: relative;
  top: 6%;
  right: 3%;
}
.nav-menu-item {
  outline: none;
  position: relative;
  left: 50%;
  transform: translate(-50%, 0);
  padding: 1.6rem;
  writing-mode: vertical-rl;
  text-align: center;
  text-decoration: none;
  font-size: 2.2rem;
  border-bottom: 0.3rem solid rgb(129, 86, 214);
  z-index: 50;
  &:hover {
    font-weight: bold;
    cursor: pointer;
  }
}

.no-decoration {
  text-decoration: none;
  color: black;
  z-index: 0;
}
.active-nav {
  text-decoration: none;
  font-weight: bold;
}
.nav-menu-last-item {
  border-bottom: none;
}

.categories-list {
  text-decoration: none;
  position: absolute;
  transform: translate(-1.5rem, -12rem);
  width: 25rem;
  display: flex;
  flex-direction: column;
  font-size: 1.7rem;
  border: 0.2rem solid rgb(129, 86, 214);
  writing-mode: horizontal-tb;
  background-color: rgb(243, 243, 243);
  text-align: left;
  list-style-type: none;
  transition: all 0.5s;
  opacity: 0;
  visibility: hidden;
}
.categories {
  &:hover {
    .categories-list {
      visibility: visible;
      opacity: 1;
    }
  }
}
.categories-list-item {
  color: black;
  text-decoration: none;
  padding: 1rem;
  &:hover {
    background-color: rgba(129, 86, 214, 0.76);
  }
}
.last {
  border-bottom: none;
}
@media (max-width: 1024px) {
  .navigation {
    display: none;
  }
}
@media (max-height: 600px) {
  .logo {
    display: none;
  }
  .nav-menu-item {
    font-size: 1.6rem;
  }
}
