.cart {
  background-color: rgb(243, 243, 243);
  display: flex;
  flex-direction: column;
  font-size: 2rem;
  grid-column: content-start/content-end;
  width: 100%;
  min-height: 100vh;
  justify-content: center;
}
.cart-items {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.cart-image {
  position: absolute;
  top: 2rem;
  left: 2rem;
  height: 20rem;
  width: 20rem;
}

.cart-container {
  margin-top: 20rem;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.cart-empty {
  width: 100%;
  //margin-top: 3rem;
  text-align: center;
}
.cart-total {
  align-self: flex-end;
  font-size: 3rem;
}
.cart-button-container {
  width: 100%;
  position: relative;
  align-self: flex-end;
  height: min-content;
  width: min-content;
  margin-top: 3rem;
  &:hover {
    .cart-button-errors {
      visibility: visible;
    }
  }
}
.cart-button-errors {
  visibility: hidden;
}
.cart-button-error {
  font-size: 1rem;
  color: red;
  text-align: right;
}
.cart-button {
  position: relative;
  border: none;
  outline: none;
  box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
    0 0.5rem 0.5rem rgba(0, 0, 0, 0.22);
  background-color: #8156d6;
  color: white;
  border-radius: 0.4rem;
  width: 30rem;
  height: 5rem;
  font-size: 2rem;
  &:hover {
    cursor: pointer;
    background-color: rgb(89, 47, 173);
  }
  &:active {
    transform: translateY(-0.2rem);

    box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
      0 0.2rem 0.2rem rgba(0, 0, 0, 0.22);
  }
  &:disabled {
    background-color: rgb(177, 174, 174);
    cursor: no-drop;
  }
}

@media (max-width: 1024px) {
  .cart {
    grid-column: page-start/page-end;
  }
}
@media (max-width: 600px) {
  .cart-image {
    height: 12rem;
    width: 12rem;
  }
  .cart-container {
    margin-top: 5rem;
  }
  .cart-empty {
    margin-top: 3rem;
    margin-bottom: 3rem;
  }
  .cart-button-container {
    margin-bottom: 10rem;
  }
}
// animations

.cart-animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
.animated-delay {
  -webkit-animation-duration: 3s;
  animation-duration: 3s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-delay: 0.5s;
  animation-delay: 0.5s;
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.fade-in {
  -webkit-animation-name: fade-in;
  animation-name: fade-in;
}
