.reset-img {
  position: absolute;
  top: 2rem;
  left: 2rem;
  height: 20rem;
  width: 20rem;
}
.reset {
  grid-column: content-start/content-end;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}
.on-reset {
  grid-column: content-start/content-end;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}
.on-reset-text {
  font-size: 3rem;
}
.on-reset-link {
  font-size: 3rem;
  text-decoration: none;
  a {
    text-decoration: none;
    color: #8156d6;
  }
}
.reset-input {
  margin-top: 1rem;
  margin-bottom: 1rem;
  width: 30rem;
  height: 5rem;
  padding: 1rem;
  font-size: 2rem;
  border: solid 1px;
  background-color: #fff;
  border-radius: 0.4rem;
  outline: none;
  &:focus {
    background-color: #a17fe4c9;
  }
}
.reset-input-error {
  color: red;
}
.reset-button {
  margin: 2rem;
  border: none;
  outline: none;
  box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
    0 0.5rem 0.5rem rgba(0, 0, 0, 0.22);
  background-color: #8156d6;
  color: whitesmoke;
  border-radius: 0.4rem;
  width: 25rem;
  height: 4rem;
  font-size: 2rem;
  &:hover {
    cursor: pointer;
    background-color: rgb(89, 47, 173);
  }
  &:active {
    transform: translateY(-0.2rem);
    box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
      0 0.2rem 0.2rem rgba(0, 0, 0, 0.22);
  }
  &:disabled {
    background-color: rgb(177, 174, 174);
    cursor: no-drop;
  }
}
.reset-to-login {
  text-decoration: none;
  color: #8156d6;
  margin-bottom: 0.5rem;
}
@media (max-width: 1024px) {
  .reset {
    grid-column: page-start/page-end;
  }
}
@media (max-width: 600px) {
  .reset-img {
    height: 12rem;
    width: 12rem;
  }
}
