.header {
  grid-column: page-start/page-end;
  width: 100%;
  height: 120vh;
  min-height: 65rem;
}
.header-image {
  width: 100%;
  min-height: 65rem;
}
.quote {
  position: absolute;
  top: 20%;
  left: 40%;
  width: 40%;
  height: 70%;
  min-height: 45rem;
  padding: 1rem;
  color: white;
  mix-blend-mode: multiply;
  background-color: rgba(129, 86, 214, 0.9);
}
.quote-text {
  text-align: center;
  font-size: 3rem;
}
.quote-link {
  position: absolute;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  height: min-content;
  bottom: 2rem;
  right: 2rem;
  font-size: 2rem;
  cursor: pointer;
  text-decoration: none;
  color: white;

  &:hover {
    color: lighten(rgb(129, 86, 214), 30%);
  }
  // text-decoration: underline;
}
.quote-link-arrow {
  height: 100%;
}

@media (max-width: 1024px) {
  .header {
    grid-column: page-start/page-end;
    height: 55vh;
  }
  .header-image {
    height: 55vh;
  }
  .quote {
    position: absolute;
    top: 5%;
    left: 40%;
    width: 50%;
    height: 40%;
    background-color: rgb(129, 86, 214);
  }
  .quote-text {
    text-align: center;
    font-size: 3rem;
  }
}
@media (max-width: 600px) {
  .header {
    grid-column: page-start/page-end;
  }
}

// animations
.header-animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
.header-animated-delay {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-delay: 0.5s;
  animation-delay: 0.5s;
}

@-webkit-keyframes fade-in-left {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-3rem);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
  }
}

@keyframes fade-in-left {
  0% {
    opacity: 0;
    transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.fade-in-left {
  -webkit-animation-name: fade-in-left;
  animation-name: fade-in-left;
}

@keyframes arrow-animation {
  0%,
  100% {
    transform: translate(0, 0);
  }

  50% {
    transform: translate(0.3rem, 0);
  }
}
.quote-link-arrow {
  animation: arrow-animation 1s ease-in-out infinite;
}
