.notification {
  position: fixed;
  z-index: 1000;
  top: 20%;
  right: 0;
  width: 40rem;
  height: 18rem;
  padding: 1rem;
  background-color: rgb(243, 243, 243);
  border: rgba(129, 86, 214, 0.95) 1rem solid;
  border-radius: 0.3rem;

  display: flex;
  justify-content: center;
  align-items: center;
}
.message {
  font-size: 3rem;
  text-align: center;
}
.my-noti-enter {
  transform: translateX(100%);
}
.my-noti-enter-active {
  transform: translateX(0%);
  transition: transform 1000ms ease-in-out;
}

.my-noti-exit {
  transform: translateX(0%);
}
.my-noti-exit-active {
  transform: translateX(100%);
  transition: transform 500ms ease-in-out;
}
.my-noti-exit-done {
  display: none;
}
