$color-primary: rgb(129, 86, 214);
.card {
  margin-top: 3rem;
  margin-bottom: 3rem;
  margin-left: 2rem;
  margin-right: 2rem;
  position: relative;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  width: 22rem;
  height: 28rem;
  padding: 1rem;
  border-radius: 0.5rem;
  box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
    0 1rem 1rem rgba(0, 0, 0, 0.22);
  overflow: hidden;
  transition: 0.3s;
  &:hover {
    .bgcontainer {
      background-size: 120%;
    }

    box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.25),
      0 0.7rem 0.7rem rgba(0, 0, 0, 0.22);
    .cover {
      opacity: 1;

      z-index: 2;
    }
    .price {
      transform: translateY(10rem);
    }
    .title {
      transform: translateY(-5rem);
    }
  }
  .bgcontainer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: 100%;
    opacity: 0.7;
    background-position: center center;
    background-repeat: no-repeat;
    overflow: hidden;
    border-radius: 0.5rem;
    transition: all 0.5s;
  }
  .cover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;

    transition: opacity 0.3s;
    opacity: 0;
    overflow: hidden;
    padding: 1rem;
    border-radius: 0.5rem;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    background-color: $color-primary;
    color: white;
    font-size: 2rem;
    text-align: center;
  }
}
.cover-container {
  min-height: 50%;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}
.price {
  opacity: 1;
  position: absolute;
  bottom: 0;
  right: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 1rem;
  background-color: darken($color-primary, 5%);
  font-size: 2rem;
  color: white;
  transition: all 0.5s ease-in;
  border-radius: 0.2rem;
  text-shadow: 1px 1px 2px rgb(0, 0, 0);
}
.title {
  color: white;
  transition: all 0.5s ease-in;
  z-index: 1;
  //margin: 0 0 5px;
  text-align: center;
  font-size: 2.5rem;

  // text-shadow: 1px 1px rgba(black, 0.8);
  text-shadow: 1px 1px 2px rgb(0, 0, 0);
  span {
    background-color: darken($color-primary, 5%);
  }
}

.card-button {
  border: white 0.4rem solid;
  background-color: #fff;
  padding: 0.4rem;
  cursor: pointer;
  color: rgb(129, 86, 214);
  height: 4rem;

  width: 16rem;
  font-size: 1.8rem;
  border-radius: 1rem;
  text-align: center;
  outline: none;
  transition: 0.3s;
  &:hover {
    transform: scale(1.1);
  }
  &:active {
    transform: translateY(-0.2rem);
    box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
      0 0.2rem 0.2rem rgba(0, 0, 0, 0.22);
  }
}
.cover-title {
  font-size: 2.5rem;
  text-shadow: rgba(0, 0, 0, 0.22);
  min-height: 40%;
}
.hor-line {
  width: 90%;
}
