.cart-item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-content: center;
  margin-top: 2rem;
  margin-left: 2rem;
  padding: 2rem;
  width: 85%;
}
.cart-left {
  display: flex;
  align-items: center;
}
.cart-right {
  display: flex;
  align-items: center;
}
.cart-item-title {
  font-size: 2.7rem;
  margin: 1.5rem;

  .cart-item-author {
    font-size: 1.8rem;
    transform: translateX(1rem);
  }
}
.cart-item-img {
  height: 20rem;
  width: 15rem;
  border-radius: 0.2rem;
  background-size: 100%;

  background-position: center center;
  background-repeat: no-repeat;
  box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
    0 0.5rem 0.5rem rgba(0, 0, 0, 0.22);
}
.cart-item-quantity {
  font-size: 2.5rem;
  margin: 1rem;
  width: 3.2rem;
  text-align: center;
}
.cart-item-button {
  box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
    0 0.5rem 0.5rem rgba(0, 0, 0, 0.22);
  background-color: rgb(129, 86, 214);
  height: 3rem;
  width: 3rem;
  margin: 0.7rem;
  color: white;
  font-size: 2.5rem;
  border-radius: 0.5rem;
  border: none;
  outline: none;
  cursor: pointer;
  &:hover {
    background-color: rgb(89, 47, 173);
  }
  &:active {
    transform: translateY(-0.2rem);
    box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
      0 0.2rem 0.2rem rgba(0, 0, 0, 0.22);
  }
}
.cart-item-remove {
  box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
    0 0.5rem 0.5rem rgba(0, 0, 0, 0.22);
  margin: 1rem;
  background-color: rgb(207, 27, 27);
  height: 4rem;
  width: 12rem;
  color: white;
  font-size: 1.5rem;
  border-radius: 0.7rem;
  border: none;
  outline: none;
  cursor: pointer;
  &:hover {
    background-color: rgb(145, 2, 2);
  }
  &:active {
    transform: translateY(-0.2rem);
    box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
      0 0.2rem 0.2rem rgba(0, 0, 0, 0.22);
  }
}
@media (max-width: 600px) {
  .cart-item {
    width: 90%;
  }
  .cart-right {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  .cart-item-img {
    height: 16rem;
    width: 12rem;
    border-radius: 0.2rem;
    background-size: 150%;
  }
  .cart-item-title {
    font-size: 2rem;
    margin: 1rem;

    .cart-item-author {
      font-size: 1.4rem;
      transform: translateX(1rem);
    }
  }
  .cart-item-button {
    margin: 0.5rem;
  }
  .cart-item-quantity {
    margin: 0.5rem;
  }
}
