.page {
  display: grid;

  // grid-template-rows:
  //   [page-start header-start] 120vh
  //   [header-end content-start genres-start] minmax(65vh, min-content)
  //   [ genres-end books-start] minmax(75vh, min-content)
  //   [ books-end authors-start] minmax(75vh, min-content)
  //   [ authors-end content-end footer-start] 20vh
  //   [footer-end page-end];
  grid-template-columns://vw replaces with % cos vw ignores sidebar
    [page-start content-start] 85%
    [content-end navigation-start] 15%
    [navigation-end page-end];
}

@media (max-width: 992px) {
  .page {
    display: grid;
    // grid-template-rows:
    //   [page-start content-start] 85vh
    //   [content-end nav-start] 15vh
    //   [ page-end nav-end];
    grid-template-columns://vw replaces with % cos vw ignores sidebar
      [page-start ] 100%
      [ page-end];
  }
}

