.authors {
  background-color: rgb(129, 86, 214);
  padding: 1rem;
  grid-column: content-start/content-end;
  display: flex;
  flex-direction: column;
}
.authors-title {
  width: 100%;
  font-size: 4rem;
  text-align: center;
  margin: 4rem 0;
}
.authors-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  align-content: space-between;
  flex-wrap: wrap;
  height: 100%;
}
.author {
  height: 100%;
  width: 33%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  text-align: center;
}
.author-img {
  height: 20rem;
  width: 20rem;
  border-radius: 8rem;
}
.author-name {
  margin-bottom: 2rem;
  font-size: 3rem;
  color: white;
}
.author-quote {
  margin: 2rem;
  color: white;
  font-size: 1.8rem;
}
.author-link {
  text-decoration: none;
}
.author-button {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 4rem;
  width: 20rem;
  margin-bottom: 4rem;
  border-radius: 1rem;
  background-color: white;
  text-align: center;
  color: rgb(141, 97, 230);
  font-size: 2rem;
  cursor: pointer;
  text-decoration: none;
  outline: none;
  border: none;
  transition: 0.3s;
  &:hover {
    transform: scale(1.1);
  }
  &:active {
    transform: translateY(-4px);
  }
}

@media (max-width: 1024px) {
  .authors {
    grid-column: page-start/page-end;
  }
}
@media (max-width: 600px) {
  .authors-container {
    display: flex;
    flex-direction: column;
  }
  .author {
    width: 100%;
    height: min-content;
  }
}
// animations
.authors-animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
.authors-animated-delay {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-delay: 0.5s;
  animation-delay: 0.5s;
}

@-webkit-keyframes fade-in-left {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-3rem);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
  }
}

@keyframes fade-in-left {
  0% {
    opacity: 0;
    transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.fade-in-left {
  -webkit-animation-name: fade-in-left;
  animation-name: fade-in-left;
}

@keyframes arrow-animation {
  0%,
  100% {
    transform: translate(0, 0);
  }

  50% {
    transform: translate(0.3rem, 0);
  }
}
.arrow {
  animation: arrow-animation 1s ease-in-out infinite;

  font-size: 1.7rem;
}
