.signup-img {
  position: absolute;
  top: 2rem;
  left: 2rem;
  height: 20rem;
  width: 20rem;
}
.signup {
  grid-column: content-start/content-end;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.signup-input {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  width: 30rem;
  height: 5rem;
  padding: 1rem;
  font-size: 2rem;
  border: solid 1px;
  background-color: #fff;
  border-radius: 0.4rem;
  outline: none;
  &:focus {
    background-color: #a17fe4c9;
  }
}
.signup-button {
  margin: 1.5rem;
  border: none;
  outline: none;
  box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
    0 0.5rem 0.5rem rgba(0, 0, 0, 0.22);
  background-color: #8156d6;
  color: whitesmoke;
  border-radius: 0.4rem;
  width: 15rem;
  height: 4rem;
  font-size: 2rem;
  &:hover {
    cursor: pointer;
    background-color: rgb(89, 47, 173);
  }
  &:active {
    transform: translateY(-0.2rem);
    box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
      0 0.2rem 0.2rem rgba(0, 0, 0, 0.22);
  }
  &:disabled {
    background-color: rgb(177, 174, 174);
    cursor: no-drop;
  }
}
.signup-to-login {
  text-decoration: none;
  color: #8156d6;
}
.signup-input-error {
  color: red;
}
.signup-error {
  color: red;
}
.signup-success {
  grid-column: content-start/content-end;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  font-size: 3rem;
  a {
    text-decoration: none;
    color: #8156d6;
  }
}
.signup-to-login {
  text-decoration: none;
  color: #8156d6;
}
@media (max-width: 1024px) {
  .signup {
    grid-column: page-start/page-end;
  }
}
@media (max-width: 600px) {
  .signup-img {
    height: 12rem;
    width: 12rem;
  }
}
// animations
.animated {
  -webkit-animation-duration: 0.5s;
  animation-duration: 0.5s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
.animated-delay {
  -webkit-animation-duration: 3s;
  animation-duration: 3s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-delay: 0.5s;
  animation-delay: 0.5s;
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.fade-in {
  -webkit-animation-name: fade-in;
  animation-name: fade-in;
}
